'use server';

import { createClient } from '@supabase/supabase-js';
import { revalidatePath } from 'next/cache';
import { z } from 'zod';

// Schema de validação com Zod para garantir a integridade dos dados antes de
// enviá-los ao banco de dados. Isso adiciona uma camada extra de segurança.
const PackageSchema = z.object({
  id: z.string().optional(),
  origem: z.string().min(1, "Origem é obrigatória."),
  destino: z.string().min(1, "Destino é obrigatório."),
  data_ida: z.string().min(1, "Data de ida é obrigatória."),
  data_volta: z.string().nullable().optional(),
  meio_transporte: z.string(),
  companhia: z.string().nullable().optional(),
  preco: z.coerce.number().positive("O preço deve ser positivo."),
  disponibilidade: z.coerce.number().int().min(0, "A disponibilidade não pode ser negativa."),
  descricao: z.string().nullable().optional(),
  imagem_url: z.string().url("URL da imagem inválida.").nullable().optional(),
  categoria: z.string().nullable().optional(),
  ativo: z.boolean(),
});

type PackageFormData = z.infer<typeof PackageSchema>;

// Cria um cliente Supabase com permissões de administrador.
const createAdminClient = () => {
  if (!process.env.NEXT_PUBLIC_SUPABASE_URL || !process.env.SUPABASE_SERVICE_KEY) {
    throw new Error('Variáveis de ambiente do Supabase não configuradas.');
  }
  return createClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL,
    process.env.SUPABASE_SERVICE_KEY
  );
};

/**
 * Busca e retorna um pacote de viagem pelo seu ID.
 */
export async function getPackageById(id: string) {
  if (!id) {
    return { error: { message: 'ID do pacote não fornecido.' } };
  }

  const supabaseAdmin = createAdminClient();
  const { data, error } = await supabaseAdmin
    .from('viagens')
    .select('*')
    .eq('id', id)
    .single(); // .single() garante que apenas um registro seja retornado

  if (error) {
    console.error(`Erro ao buscar pacote com ID ${id}:`, error.message);
    return { error };
  }

  return { pkg: data };
}

/**
 * Busca e retorna todos os pacotes de viagem.
 */
export async function getPackages() {
  const supabaseAdmin = createAdminClient();
  const { data, error } = await supabaseAdmin
    .from('viagens')
    .select('*')
    .order('criado_em', { ascending: false });

  if (error) {
    console.error('Erro ao buscar pacotes:', error.message);
    return { error };
  }
  return { packages: data };
}

/**
 * Cria um novo pacote de viagem.
 */
export async function createPackage(pkgData: PackageFormData) {
  const validated = PackageSchema.safeParse(pkgData);
  if (!validated.success) {
    return { error: { message: validated.error.flatten().fieldErrors }};
  }
  
  const supabaseAdmin = createAdminClient();
  const { error } = await supabaseAdmin.from('viagens').insert(validated.data);
  
  if (error) {
    console.error("Erro ao criar pacote:", error.message);
    return { error };
  }

  revalidatePath('/admin/packages');
  return { success: true };
}

/**
 * Atualiza um pacote de viagem existente.
 */
export async function updatePackage(pkgData: PackageFormData) {
  const validated = PackageSchema.safeParse(pkgData);
  if (!validated.success || !validated.data.id) {
    return { error: { message: validated.success ? "ID do pacote é necessário." : validated.error.flatten().fieldErrors }};
  }

  const { id, ...updateData } = validated.data;
  
  const supabaseAdmin = createAdminClient();
  const { error } = await supabaseAdmin.from('viagens').update(updateData).eq('id', id);

  if (error) {
    console.error("Erro ao atualizar pacote:", error.message);
    return { error };
  }

  revalidatePath('/admin/packages');
  revalidatePath(`/packages/${id}`); // Invalida a página de detalhes também
  return { success: true };
}

/**
 * Deleta um pacote de viagem.
 */
export async function deletePackage(packageId: string) {
  if (!packageId) {
    return { error: { message: "ID do pacote é necessário." } };
  }
  
  const supabaseAdmin = createAdminClient();
  const { error } = await supabaseAdmin.from('viagens').delete().eq('id', packageId);

  if (error) {
    console.error("Erro ao deletar pacote:", error.message);
    return { error };
  }

  revalidatePath('/admin/packages');
  return { success: true };
}
